
.main-frame {
  width: 71%;
  margin: 0 auto;
}
.updateTableFrame {
  margin-top: 71px;
  width: 100%;
}
.updateTableFrame th {
  border: 1px solid blue;;
}
.updateTableTitleUniversal {
  background-color: brown;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  color: beige;
  height: 31px;
}
.updateTableTitleErste {
  width: 23%;
  padding: 11px;
}
.updateTableContent {
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
  font-size: 19px;
}
.updateTableContentJedes {
  padding: 17px;
  cursor: pointer;
}
.updateTableContentJedes:hover {
  font-weight: 600;
  color: brown;
}
.jedesUpdateFrame {
  margin-top: 31px;
