}
.jedesUpdateFieldTitle {
  font-family:'Courier New', Courier, monospace;
  font-size: 21px;
  width: 17%;
}
.jedesUpdateFieldImport {
  font-size: 21px;
}
.slideShowFrame {
  margin: 7px;
  width: 100%;
}
.sliderExample {
  text-align: center;
  font-size: 31px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  margin: 17px;
}
.slideShowContainer {
  margin-top: 11px;
}
.slideShowjedesBild {
